<template>

  <div>
  <el-row>
    <img class="logo" style="width: 150px; height: 150px" src="../assets/1.jpg" alt=""/>
    <div class="h1">NETWORK TOOLS</div>
  </el-row>
  <el-row>
  <el-col :span="6" :offset="3" >
    <el-row  type="flex" class="row-bg" gutter="20">
     <div>
      <el-button type="primary" plain @click="ClickPing">Ping</el-button>
       </div>
    </el-row>
     <el-row type="flex" class="row-bg"  gutter="20">
     <div class="grid-content">
      <el-button type="success"  plain @click="ClickTraceroute">Traceroute</el-button>
       </div>
    </el-row>
    <el-row type="flex" class="row-bg" gutter="20">
     <div class="grid-content">
        <el-button type="info" plain @click="ClickFTPCheck">FTP Check</el-button>
       </div>
    </el-row>
    <el-row type="flex" class="row-bg" gutter="20">
     <div class="grid-content">
      <el-button type="warning" plain @click="ClickShellCheck">Shell Check</el-button>
       </div>
    </el-row>
    </el-col>
  <el-col :span="15"  >
    <el-row gutter="20" type="flex" class="row-bg">
    <div v-if="flag == 1" >
    <el-input   v-model="form.input" style="width:400px" placeholder="Address" clearable></el-input>
    <el-button type="danger" class="n" @click="Submit1" >Submit</el-button>
    </div>
    <div v-if="flag == 2" >
    <el-input   v-model="form.input" style="width:400px" placeholder="Address" clearable></el-input>
    <el-button type="danger" class="n" @click="Submit2" >Submit</el-button>
    </div>
    <div v-if="flag == 4" >
    <el-input   v-model="form.input" style="width:400px" placeholder="Shell" clearable></el-input>
    <el-button type="danger" class="n" @click="Submit4" >Submit</el-button>
    </div>
    <!-- <div v-if="flag==3">
    <el-button type="danger" class="n2" @click="Submit3" >提交</el-button>
    </div> -->
      </el-row>
      <el-row gutter="20" type="flex" class="row-bg">
         <el-input
      style="width:500px"
      type="textarea"
      :rows="10"
      placeholder="Result"
      v-model="result"
      >
    </el-input>
      </el-row>
  </el-col>
  </el-row>
  </div>
</template>

<script>
import {Ping,Traceroute,FTP,Shell} from "../api/api";
export default {
  data () {
    return {
      flag: 1,
      form: {
        input: '',
      },
      result: ''
    }
  },
methods:{
  ClickPing() {
    this.result=''
    this.flag=1
  },
  ClickTraceroute() {
    this.result=''
    this.flag=2
  },
  ClickFTPCheck() {
    this.result=''
    Submit3()
  },
  ClickShellCheck() {
    this.result=''
    this.flag=4
  },

  Submit1() {
    Ping(this.form.input).then((res) => {
      console.log(res.data)
      this.result=res.data
    })
  },
  Submit2() {
    Traceroute(this.form.input).then((res) => {
      console.log(res.data)
      this.result=res.data
    })
  },
  Submit3() {
    FTP().then((res) => {
      console.log(res.data)
      this.result=res.data
    })
  },
  Submit4() {
    Shell(this.form.input).then((res) => {
      console.log(res.data)
      this.result=res.data
    })
  }
}
  }
</script>
<style scoped>
.el-row {
    margin-bottom: 20px;
  }
.el-button{
  width:200px;
  height:50px;
  font-size: 25px;
}
.n{
  margin-left:10px;
  width:95px;
  height:40px;
  font-size: 15px;
}
.n2{
  /* margin-left:200px; */
  width:95px;
  height:40px;
  font-size: 15px;
}
.h1{
  font-size:50px ;
  color: #42b983;
}

ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



